#navigation-bar {
  z-index: 99;
  position: relative;
  border-bottom: solid 2px #eee;
}
#navigation-bar .navbar-brand {
  max-width: 80%;
}
#navigation-bar .navbar-toggler {
  outline: 5px auto -webkit-focus-ring-color;
  background-color: #f1f1f1;
}
#navigation-bar .nav-item a {
  color: #687074;
  font-size: 15px;
  font-weight: 400;
  text-transform: uppercase;
  text-align: center;
}
#navigation-bar .nav-item a:hover {
  color: #fff;
  background: #00008b;
}

@media (max-width: 991px) {
  #navigation-bar .nav-item > a {
    font-size: 14px;
    padding: 9px 10px !important;
    text-align: center;
    border-bottom: solid 1px #eee !important;
  }

  #navigation-bar .nav-item > a:first-child {
    border-top: solid 1px #eee !important;
  }
}

@media (min-width: 992px) {
  #navigation-bar .navbar-collapse {
    margin-left: auto;
    margin-right: auto;
    padding-left: 9rem !important;
  }
  #navigation-bar .nav-collapse {
    padding: 0;
    position: relative;
    max-height: inherit;
    border-top: 1px solid #eee;
    border-bottom: 1px solid #eee;
  }
  #navigation-bar .navbar-nav {
    top: 1px;
    float: left;
    position: relative;
  }
  #navigation-bar .navbar-nav > li {
    margin-left: -1px;
    border-left: 1px solid #eee;
    border-right: 1px solid #eee;
  }
  #navigation-bar .nav-item a {
    font-size: 13px;
    padding: 10px 30px 10px 20px !important;
    color: #687074;
    font-weight: 400;
    text-transform: uppercase;
    border-top: solid 2px transparent;
    border-bottom: solid 2px transparent;
  }
}
